@font-face {
    font-family: 'G';
    src: url('./fonts/Geometria.otf'); }
@font-face {
    font-family: 'GB';
    src: url('./fonts/Geometria-Bold.otf'); }

$main: #631AEE;

* {
    box-sizing: border-box; }

body {
    background-color: #f3f3f3;
    font-family: G; }
input,
select,
label,
textarea,
button {
    font-family: G; }

.table {
    margin: 50px auto;
    max-width: 1200px; }

.col-wrapper {
    margin: 5px;
    &[width="100"] {
        flex-basis: 100%; }
    &[width="50"] {
        flex-basis: 50%; } }
.th {
    text-align: center;
    background-color: $main;
    border-radius: 10px;
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: space-around;
    span {
        width: 100%; }
    .options {
        display: flex;
        padding: 5px;
        font-size: 22px;
        i {
            cursor: pointer;
            margin: 3px;
            opacity: .85;
            transition: all .3s;
            &:hover {
                opacity: 1; } } } }
.cell-wrapper {
    display: flex;
    align-items: center;
    label {
        width: 100%; }
    &[disabled] {
        label,i {
            opacity: .4; } }
    &:nth-child(2) {
        margin-top: 10px; }
    & i {
        font-size: 20px;
        cursor: pointer; } }

i.bi-x {
    color: red;
    font-weight: bolder; }
i.bi {
    color: $main; }

.cell-input {
    padding: 10px 15px;
    border: 1px solid #ddd;
    margin: 3px 0;
    border-radius: 5px;
    background-color: #fff;
    font-size: 0.8rem;
    width: 100%; }

.table {
    display: flex;
    justify-content: center; }

.before {
    i {
        transform: all 0.3s;
        margin: 0 5px;
        color: $main;
        &:hover {
            color: $main; } } }
